@font-face {
  font-family: 'avenirnextltpro';
  src: url('fonts/avenirnextltpro/AvenirNextLTPro-Bold.eot');
  src: url('fonts/avenirnextltpro/AvenirNextLTPro-Bold.woff') format('woff'),
  url('fonts/avenirnextltpro/AvenirNextLTPro-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;

}

@font-face {
  font-family: 'avenirnextltpro';
  src: url('fonts/avenirnextltpro/AvenirNextLTPro-Regular.eot');
  src: url('fonts/avenirnextltpro/AvenirNextLTPro-Regular.woff') format('woff'),
  url('fonts/avenirnextltpro/AvenirNextLTPro-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;

}

/*Variables*/

$primary-color:#066CAF;
$gradient-color:#308fcf;
$card-color:#368ECA;


.title-container {
  position: relative;
  z-index: -5;
}
.title {
  position:relative;
  width:40%;
  padding:1em;
  background: radial-gradient($gradient-color 33%, $primary-color);
  box-shadow: inset -6px -2px 12px -4px rgba(255,255,255,1)
}

.title:before,
.title:after {
  content:"";
  position:absolute;
  z-index:-2;
}

.curva:before {
  box-shadow:0 0 15px rgba(0,0,0,0.6);
  top:50%;
  bottom:0;
  left:10px;
  right:10px;
  border-radius:185px / 35px;
}


/* Universal Selectors */
/* Element Selectors */
body {
  margin:0;
  height: 100%;
  background-color: $primary-color;
  background: radial-gradient($gradient-color 33%, $primary-color);
  background-repeat: no-repeat;
  background-attachment: fixed;
  font-family: "AvenirNextLTPro", verdana, arial, sans-serif;
}


/* Class, Attribute, and Pseudo Selectors */

.title {
  width: 100%;
  text-align: center;
  color: whitesmoke;
  //border-bottom: 1px solid black;
}

.title h2{
  text-shadow: 0 -1px 0 black;
}

.title .sub-title{
  font-size: 25px;
}

.doctors{
  margin-top: 35px;
}

.doctors .padding-doctor{
  width: 49%;
  display: inline-block;
  box-sizing: border-box;
  min-width: 320px;
  min-height: 50px !important;
  //background-color: white;
  overflow: hidden;
  height: 105px;;

}

.padding-doctor .doctor-card{
  border-radius: 90px 90px 90px 90px;
  background-color: $card-color;
  width: 75%;
  min-width: 320px;
  max-width: 460px;
  margin: 0 auto;
  height: 103px;
  position: relative;
  box-shadow: inset 1px 1px 5px 0px rgba(255, 255, 255, 1), inset -2px -2px 5px #5EB0E6, 1px 1px 5px #2F2D2D;



}

.border-image{
  width: 80px;
  height: 80px;
  overflow: hidden;
  border-radius: 50%;
  position: relative;
  top: 10%;
  left: 4%;
  border-style: solid;
  border-color: white;
}

.doctor-card .doctor-name{
  top: 5%;
  position: absolute;
  left: 115px;
  color: white;
  text-shadow: 1px 1px rgb(0, 0, 0);
  /*for the elipsis*/
  white-space: nowrap;
  width: 15em;
  overflow: hidden;
  text-overflow: ellipsis;

}

.doctor-card .doctor-code{
  top: 40%;
  position: absolute;
  left: 115px;
  color: white;
  text-shadow: 1px 1px rgb(0, 0, 0);
  /*for the elipsis*/
  white-space: nowrap;
  width: 15em;
  overflow: hidden;
  text-overflow: ellipsis;

}


